@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';
/* @import 'iconfont.css';
@import 'tocbot.css'; */

/* 自定义基础样式 */
@layer base {
  html {
    scroll-behavior: smooth;
  }
  
  body {
    @apply font-sans text-secondary-900 bg-white dark:text-secondary-100 dark:bg-secondary-900 transition-colors duration-300;
  }
  
  /* 滚动条样式 */
  ::-webkit-scrollbar {
    @apply w-2;
  }
  
  ::-webkit-scrollbar-track {
    @apply bg-secondary-100 dark:bg-secondary-800;
  }
  
  ::-webkit-scrollbar-thumb {
    @apply bg-secondary-300 dark:bg-secondary-600 rounded-full;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    @apply bg-secondary-400 dark:bg-secondary-500;
  }
}

/* 组件样式 */
@layer components {
  /* 按钮样式 */
  .btn {
    @apply inline-flex items-center justify-center px-4 py-2 text-sm font-medium rounded-lg transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2;
  }
  
  .btn-primary {
    @apply btn bg-primary-600 text-white hover:bg-primary-700 focus:ring-primary-500 shadow-soft hover:shadow-medium;
  }
  
  .btn-secondary {
    @apply btn bg-secondary-100 text-secondary-900 hover:bg-secondary-200 focus:ring-secondary-500 dark:bg-secondary-800 dark:text-secondary-100 dark:hover:bg-secondary-700;
  }
  
  .btn-accent {
    @apply btn bg-accent-600 text-white hover:bg-accent-700 focus:ring-accent-500;
  }
  
  /* 卡片样式 */
  .card {
    @apply bg-white dark:bg-secondary-800 rounded-xl shadow-soft border border-secondary-200 dark:border-secondary-700 transition-all duration-200 hover:shadow-medium;
  }
  
  /* 输入框样式 */
  .input {
    @apply w-full px-3 py-2 text-sm border border-secondary-300 dark:border-secondary-600 rounded-lg bg-white dark:bg-secondary-800 text-secondary-900 dark:text-secondary-100 placeholder-secondary-500 dark:placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all duration-200;
  }
  
  /* 徽章样式 */
  .badge {
    @apply inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium;
  }
  
  .badge-hot {
    @apply badge bg-accent-100 text-accent-800 dark:bg-accent-900 dark:text-accent-200;
  }
  
  .badge-new {
    @apply badge bg-success-100 text-success-800 dark:bg-success-900 dark:text-success-200;
  }
  
  /* 导航样式 */
  .nav-link {
    @apply text-secondary-700 dark:text-secondary-300 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-200;
  }
  
  .nav-link-active {
    @apply text-primary-600 dark:text-primary-400 font-medium;
  }
  
  /* 下拉菜单样式 */
  .dropdown {
    @apply absolute top-full left-0 mt-2 w-48 bg-white dark:bg-secondary-800 rounded-lg shadow-large border border-secondary-200 dark:border-secondary-700 opacity-0 invisible transform scale-95 transition-all duration-200 origin-top-left;
  }
  
  .dropdown.show {
    @apply opacity-100 visible transform scale-100;
  }
  
  .dropdown-item {
    @apply block w-full px-4 py-2 text-sm text-secondary-700 dark:text-secondary-300 hover:bg-secondary-100 dark:hover:bg-secondary-700 hover:text-secondary-900 dark:hover:text-secondary-100 transition-colors duration-200;
  }
  
  /* 移动端菜单样式 */
  .mobile-menu {
    @apply absolute top-full left-0 right-0 bg-white dark:bg-secondary-900 border-t border-secondary-200 dark:border-secondary-700 shadow-lg transform scale-y-0 origin-top transition-transform duration-300 ease-in-out;
    max-height: 80vh;
    overflow-y: auto;
  }
  
  .mobile-menu.show {
    @apply scale-y-100;
  }
  
  /* 搜索框样式 */
  /* 搜索容器样式 */
  .search-container {
    @apply relative;
  }
  
  /* 搜索按钮样式 */
  .search-button {
    @apply p-2 rounded-lg text-secondary-600 dark:text-secondary-400 hover:bg-secondary-100 dark:hover:bg-secondary-800 transition-all duration-200 flex items-center justify-center;
  }
  
  /* 搜索输入框容器 */
  .search-input-container {
    @apply absolute right-0 top-0 opacity-0 pointer-events-none transition-all duration-300 ease-in-out;
    width: 0;
  }
  
  /* 搜索输入框展开效果 */
  .search-container.show .search-input-container {
    @apply opacity-100 pointer-events-auto;
  }
  
  /* 桌面端搜索输入框宽度 */
  .search-container.show .search-input-container {
    width: 280px;
  }
  
  /* 移动端搜索输入框宽度 - 刚好盖住logo */
  .lg\:hidden .search-container.show .search-input-container {
    width: calc(100vw - 2rem - 120px); /* 减去padding和logo宽度 */
  }
  
  /* 搜索输入框样式 */
  .search-input {
    @apply w-full pl-4 pr-4 py-2 bg-white dark:bg-secondary-800 border border-secondary-200 dark:border-secondary-700 rounded-lg text-secondary-900 dark:text-secondary-100 placeholder-secondary-500 dark:placeholder-secondary-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent transition-all duration-200;
  }
  
  /* 主题切换按钮样式 */
  .theme-toggle {
    @apply p-2 rounded-lg text-secondary-600 dark:text-secondary-400 hover:bg-secondary-100 dark:hover:bg-secondary-800 transition-colors duration-200;
  }
  
  /* 动画类 */
  .animate-fade-in {
    animation: fadeIn 0.5s ease-in-out;
  }
  
  .animate-slide-up {
    animation: slideUp 0.3s ease-out;
  }
  
  .animate-scale-in {
    animation: scaleIn 0.2s ease-out;
  }
}

/* 工具类 */
@layer utilities {
  /* 文本渐变 */
  .text-gradient {
    @apply bg-gradient-to-r from-primary-600 to-accent-600 bg-clip-text text-transparent;
  }
  
  /* 背景渐变 */
  .bg-gradient-primary {
    @apply bg-gradient-to-r from-primary-600 to-primary-700;
  }
  
  .bg-gradient-accent {
    @apply bg-gradient-to-r from-accent-600 to-accent-700;
  }
  
  /* 玻璃效果 */
  .glass {
    @apply bg-white/80 dark:bg-secondary-900/80 backdrop-blur-md border border-white/20 dark:border-secondary-700/20;
  }
  
  /* 响应式容器 */
  .container-responsive {
    @apply max-w-7xl mx-auto px-4 sm:px-6 lg:px-8;
    max-width: var(--container-width, 80rem); /* 使用CSS变量，默认值为配置中的 container_width */
  }
  
  /* 安全区域 */
  .safe-area-top {
    padding-top: env(safe-area-inset-top);
  }
  
  .safe-area-bottom {
    padding-bottom: env(safe-area-inset-bottom);
  }
}

/* 下拉菜单样式 */
.dropdown-menu {
  @apply absolute top-full left-0 mt-2 w-48 bg-white dark:bg-secondary-800 rounded-lg shadow-large border border-secondary-200 dark:border-secondary-700 opacity-0 invisible transform scale-95 transition-all duration-200 origin-top-left;
}

.dropdown-menu.show {
  @apply opacity-100 visible transform scale-100;
}

/* 移动端下拉菜单 */
.mobile-dropdown-menu {
  @apply ml-4 mt-2 border-l-2 border-secondary-200 dark:border-secondary-700 pl-4;
}

/* 语言切换下拉菜单 */
.language-dropdown {
  @apply absolute bottom-full right-0 mb-2 w-48 bg-white dark:bg-secondary-800 rounded-lg shadow-large border border-secondary-200 dark:border-secondary-700 opacity-0 invisible transform scale-95 transition-all duration-200 origin-bottom-right;
}

.language-dropdown.show {
  @apply opacity-100 visible transform scale-100;
}

.language-option {
  @apply flex items-center justify-between px-4 py-3 text-secondary-700 dark:text-secondary-300 hover:bg-secondary-100 dark:hover:bg-secondary-700 cursor-pointer transition-colors duration-200;
}

.language-option.selected {
  @apply text-secondary-900 dark:text-secondary-100 font-medium;
}

.language-option:first-child {
  @apply rounded-t-lg;
}

.language-option:last-child {
  @apply rounded-b-lg;
}

.language-arrow.rotate {
  transform: rotate(180deg);
}

/* 自媒体模块样式 */
.social-media-section {
  @apply mb-6;
}

.social-media-item {
  @apply relative cursor-pointer;
}

.footer-social-icon {
  @apply w-10 h-10 rounded-full bg-secondary-200 dark:bg-secondary-700 flex items-center justify-center text-secondary-600 dark:text-secondary-400 transition-all duration-300 ease-in-out;
}

.footer-social-icon:hover {
  /* @apply transform scale-110; */
  background-color: var(--hover-color);
  color: white;
}

/* 自媒体图标字体大小 */
.footer-social-icon .icon {
  font-size: 20px;
}



/* 搜索功能样式 */
.search-container {
  @apply relative;
}

.search-button {
  @apply p-2 rounded-lg text-secondary-600 dark:text-secondary-400 hover:bg-secondary-100 dark:hover:bg-secondary-800 transition-colors duration-200;
}

.search-input-container {
  @apply absolute right-0 top-0 opacity-0 invisible pointer-events-none transition-all duration-300;
  @apply transform scale-95 origin-top-right;
}

.search-container.show .search-input-container {
  @apply opacity-100 visible pointer-events-auto transform scale-100;
}

.search-input {
  @apply px-4 py-2 text-sm bg-white dark:bg-secondary-800 border border-secondary-200 dark:border-secondary-700 rounded-lg;
  @apply text-secondary-900 dark:text-secondary-100 placeholder-secondary-500 dark:placeholder-secondary-400;
  @apply focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent;
  @apply shadow-lg;
}

/* 搜索结果 */
.search-results {
  @apply absolute top-full right-0 mt-2 bg-white dark:bg-secondary-800 rounded-lg shadow-large border border-secondary-200 dark:border-secondary-700 max-h-96 overflow-y-auto z-50;
  width: 320px;
}

.search-results.active {
  @apply block;
}

.search-result-item {
  @apply p-3 border-b border-secondary-100 dark:border-secondary-700 last:border-b-0;
  @apply hover:bg-secondary-50 dark:hover:bg-secondary-700 transition-colors duration-200;
  @apply cursor-pointer;
}

.search-result-item:last-child {
  @apply border-b-0;
}

.search-result-title {
  @apply text-sm font-medium text-secondary-900 dark:text-secondary-100 mb-1;
  @apply line-clamp-1;
}

.search-result-summary {
  @apply text-xs text-secondary-600 dark:text-secondary-400 mb-2;
  @apply line-clamp-2;
}

.search-result-meta {
  @apply flex items-center justify-between text-xs text-secondary-500 dark:text-secondary-400;
}

.search-result-tags {
  @apply flex flex-wrap gap-1;
}

.search-result-tag {
  @apply px-2 py-0.5 bg-primary-100 dark:bg-primary-900 text-primary-700 dark:text-primary-300 rounded text-xs;
}

.search-highlight {
  @apply bg-yellow-200 dark:bg-yellow-800 text-yellow-900 dark:text-yellow-100 px-1 rounded;
}

.search-no-results {
  @apply p-4 text-center text-secondary-500 dark:text-secondary-400 text-sm;
}

.search-loading {
  @apply p-4 text-center text-secondary-500 dark:text-secondary-400 text-sm;
}


/* 移动端搜索结果 - 占满100%宽度 */
.lg\:hidden .search-results {
  width: 100%;
  right: 0;
}

@media (max-width: 768px) {
  .search-input {
    @apply w-72;
  }
  
  .search-results {
    @apply w-72;
  }
}

/* 滚动到顶部按钮 */
#scroll-top {
  @apply opacity-0 invisible transform translate-y-5 transition-all duration-300;
}

#scroll-top.show {
  @apply opacity-100 visible transform translate-y-0;
}

/* 导航栏滚动效果 */
header.scrolled {
  @apply shadow-lg;
  background-color: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(20px);
}

.dark header.scrolled {
  background-color: rgba(17, 24, 39, 0.95);
}

/* Tocbot 样式覆盖 */
.toc-container {
  @apply sticky top-4;
}

.toc-link {
  @apply text-secondary-600 dark:text-secondary-400 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-200;
  @apply text-sm leading-relaxed;
}

.toc-link.is-active-link {
  @apply text-primary-600 dark:text-primary-400 font-medium;
}

.toc-list {
  @apply space-y-1;
}

.toc-list-item {
  @apply text-sm;
}

.toc-list-item.is-active-li > .toc-link {
  @apply text-primary-600 dark:text-primary-400 font-medium;
}

.toc-list .toc-list {
  @apply ml-4 mt-1;
}

/* 打印样式 */
@media print {
  .no-print {
    display: none !important;
  }
  
  body {
    @apply text-black bg-white;
  }
  
  .card {
    @apply shadow-none border border-gray-300;
  }
}
/* tab 样式 */
.tab-btn {
  /* @apply transition-all duration-200; */
}
.tab-btn:not(:last-child) {
  margin-right: 1.5rem;
}
.tab-btn.active {
  @apply text-primary-600 dark:text-primary-400 font-bold border-b-2 border-primary-600 text-lg;
}

/* 分类筛选按钮 */
.category-filters {
  @apply mb-6;
}

.category-btn {
  @apply px-4 py-2 text-sm font-medium rounded-full transition-all duration-200;
  @apply bg-white dark:bg-secondary-800 text-secondary-700 dark:text-secondary-300;
  @apply border border-secondary-200 dark:border-secondary-600;
  @apply hover:bg-primary-50 dark:hover:bg-primary-900 hover:border-primary-300 dark:hover:border-primary-600;
  @apply flex items-center;
}

.category-btn.active {
  @apply bg-primary-600 text-white border-primary-600;
  @apply hover:bg-primary-700 hover:border-primary-700;
}

.category-btn .count {
  @apply bg-secondary-100 dark:bg-secondary-700 text-secondary-600 dark:text-secondary-400;
  @apply text-xs px-2 py-0.5 rounded-full;
  @apply ml-2;
  @apply font-medium;
}

.category-btn.active .count {
  @apply bg-white bg-opacity-20 text-white;
}