.js-toc>.toc-list {
	overflow: hidden;
	position: relative;
    padding-left: 0.75rem;
    padding-right: 1rem;
}

.js-toc>.toc-list li {
	list-style: none
}

.toc-list {
	margin: 0;
	padding-left: 10px
}

a.toc-link {
	color: currentColor;
	height: 100%;
    padding: .25rem .5rem;
    display: inline-block;
	overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    box-sizing: border-box;
    width: 100%;
	transition: all 0.3s ease;
	border-radius: 4px;
}
a.toc-link:hover {
	color: rgb(37 99 235 / var(--tw-text-opacity, 1));
	background-color: rgba(113, 134, 157, 0.08);
}

.is-collapsible {
	max-height: 1000px;
	overflow: hidden;
	transition: all 300ms ease-in-out
}

.is-collapsed {
	max-height: 0
}

.is-collapsed:has(:focus) {
	max-height: 1000px
}

.is-position-fixed {
	position: fixed !important;
	top: 0
}

.is-active-link {
	font-weight: 700;
	color: rgb(37 99 235 / var(--tw-text-opacity, 1)) !important;
}

.toc-link::before {
	/* background-color: #eee;
	content: " ";
	display: inline-block;
	height: inherit;
	left: 0;
	margin-top: -1px;
	position: absolute;
	width: 2px; */

    content: '';
    position: absolute;
    left: 0;
	margin-top: .285rem;
    width: 0;
    height: 0;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-left: 6px solid transparent;
    z-index: 1;
}

.is-active-link::before {
	border-left-color: rgb(37 99 235 / var(--tw-text-opacity, 1))
}
